                  </div>
                  <div class="chat-num-messages">
                    <div v-if="typing" class="ma-1 red--text">
                      <strong> {{ typs }} is typing</strong>
                    </div>
                  </div>
                </div>
                <i class="fa fa-star" style="clear:both"></i>
              </div>
              <!-- end chat-header -->
              <v-container class="white--text pa-1  chat_messages">
                <div v-if="lenData" class="text-center">
