<script>
export default {
  name: "Home",
  data() {
    return {
      onlineUser: [],
      getData: [],
      msg: "",
      typing: false,
      typs: "",
      groupes: "",
      chating: false,
      lenData: false,
      para: "",
      recevName: "",
    };
  },

  sockets: {
    connect: function() {
      console.log("socket connected");
    },

    recev: function(data) {
      this.soundEffect.play();
      this.getData.push(data);
      this.scrollBottom();
      this.lenData = false;
    },
    onlineuser: function(data) {
      this.onlineUser.push(data);
    },
    oflineuser: function(data) {
      this.onlineUser.shift(data);
    },

    // connect_error: async (err) => {
    //   console.log(err);
    //   // this.logout;
    // },

    typings(data) {
      clearTimeout(this.timeOutId);
      this.typing = true;
      this.typs = data.nickname;
      this.timeOutId = setTimeout(() => {
        this.typing = false;
      }, 2000);
      return data;
    },
  },
  methods: {
    emitTyping() {
      this.scrollBottom();
      this.$socket.emit("typing", {
        nickname: this.$store.getters.getUserName,
      });
    },
    async logout() {
      // this.$socket.close();
      await this.$store.dispatch("destroySession");
    },
    sendMessage: async function() {
      if (this.msg.trim() != "") {
        this.$socket.emit("add", {
          msgchat: this.msg,
          userId: this.$store.getters.getUserId,
          username: this.$store.getters.getUserName,
          createdAt: this.$moment().format("YYYY-MM-DD HH:mm:ss"),
          groupe: this.groupes,
        });
        this.lenData = false;
        this.getData.push({
          msgchat: this.msg,
          userId: this.$store.getters.getUserId,
          user: { username_u: this.$store.getters.getUserName },
          createdAt: this.$moment().format("YYYY-MM-DD HH:mm:ss"),
        });
        this.scrollBottom();
        this.sendInDataBase(this.msg);
        this.msg = "";
      } else {
        //  empty
      }
    },

    sendInDataBase: async function(msg) {
      let AuthStr = "Bearer ".concat(this.$store.getters.getToken);
      let data = await this.$http.post("/c/add", {
        headers: {
          Authorization: AuthStr,
          "Access-Control-Allow-Origin": "*",
          "Content-Type": "application/json",
        },
        msgchat: msg,
        userId: this.$store.getters.getUserId,
        receverId: this.para,
        senderId: this.$store.getters.getUserId,
      });
      return data;
    },
    scrollBottom: () => {
      let messagesContainer = document.querySelector(".chat_messages");
      setTimeout(() => {
        messagesContainer.scrollTop = messagesContainer.scrollHeight;
      }, 0);
    },
    GetUsers: async function() {
      let users = await this.$http.get(
        "/auth/user/" + this.$store.getters.getUserId
      );
      this.onlineUser = users.data;
    },
    finUser: function(para, name) {
      this.para = para;
      this.recevName = name;
      this.chating = true;
      this.chatUsers(para);
      this.addRelation(para);
    },
    chatUsers: function(para) {
      let AuthStr = "Bearer ".concat(this.$store.getters.getToken);
      this.$http
        .get(`/c/chat/${para}/${this.$store.getters.getUserId}`, {
          headers: {
            Authorization: AuthStr,
            "Access-Control-Allow-Origin": "*",
            "Content-Type": "application/json",
          },
        })
        .then((res) => {
          if (res.data.length == 0) {
            this.lenData = true;
          } else {
            this.lenData = false;
          }

          this.getData = res.data;
          this.scrollBottom();
        });
    },
    addRelation: function(para) {
      let time = Date.now();
      this.$http
        .post("/c/addRel", {
          relId: this.$store.getters.getUserId + para + time,
          senderId: this.$store.getters.getUserId,
          receverId: para,
        })
        .then((res) => {
          this.groupes = res.data.urlgroupe;
          // this.$socket.emit("online", { userId: this.$store.getters.getUserName });
          this.$socket.emit("joinGroup", res.data.urlgroupe);
        });
    },
  },
  mounted() {
    this.GetUsers();
  },
};
</script>
