import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import "./plugins/axios";
import "@mdi/font/css/materialdesignicons.css";
import store from "./store";
import patterns from "./store/patterns";
import vuetify from "./plugins/vuetify";
import VueTimeago from "vue-timeago";
import Notifications from "vue-notification";
import io from "socket.io-client";
import VueSocketIO from "vue-socket.io";
import VueMoment from "vue-moment";
import moment from "moment-timezone";

Vue.use(VueMoment, {
  moment,
});
Vue.moment().locale("en");
Vue.use(
  new VueSocketIO({
    debug: true,
    // https://chat.sp0meur.com
    connection: io("wss://chat.sp0meur.com", {
      extraHeaders: {
        "x-auth-token": store.getters.getToken,
      },
      // transports: ["websocket", "polling"],
    }),

    vuex: {
      store,
      actionPrefix: "SOCKET_",
      mutationPrefix: "SOCKET_",
    },
  })
);
Vue.use(VueTimeago, {
  name: "timeago",
  locales: {
    en: require("date-fns/locale/en"),
    ar: require("date-fns/locale/ar"),
  },
});
Vue.prototype.soundEffect = new Audio(
  "https://static.xx.fbcdn.net/rsrc.php/yy/r/XFhtdTsftOC.ogg"
);
Vue.use(Notifications);
Vue.config.productionTip = false;
Vue.prototype.$pattern = patterns;
new Vue({
  router,
  store,
  vuetify,

  render: (h) => h(App),
}).$mount("#app");
